import React, { useEffect, useRef, useState } from 'react';
import { Form, Row, Col, message, Switch } from 'antd';
import Save from '@/components/save';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import Editor from 'for-editor';
import { AxiosResponse } from 'axios';
interface DataConter {
  _id: string;
  isChecked: boolean;
  updateTime: string;
  createTime: string;
  content: string;
}
interface AboutData {
  data: DataConter[];
}
const About = (props: any) => {
  // è¡¨å•æ•°æ®
  const [form] = Form.useForm();
  // è·å–æ¥å£æ•°æ®
  const [aboutData, setAboutData] = useState<DataConter[]>([]);
  // å®šä¹‰ref
  const editorRef = useRef<Editor>(null);
  // ä¸Šæ¬¡ä¿å­˜æ—¶é—´
  const [updateTime, setUpdateTime] = useState<string>();
  // æ˜¯å¦é€‰ä¸­
  const [isChecked, setIsChecked] = useState(false);
  // å…³äºåˆ—è¡¨
  useEffect(() => {
    props.BlogActions.asyncAboutListAction(isChecked).then((res: AxiosResponse<AboutData>) => {
      let { data } = res.data;
      let updateTime = data.map((item) => item.updateTime).join('');
      let createTime = data.map((item) => item.createTime).join('');
      let content = data.map((item) => item.content).join('');
      setTimeout(() => {
        form.setFieldsValue({ content: content });
      }, 500);
      setUpdateTime(updateTime === '0' ? createTime : updateTime);
      setAboutData(data);
    });
  }, [form, isChecked, props.BlogActions]);

  // ä¿å­˜
  const submit = async () => {
    await form.validateFields();
    const val = await form.getFieldsValue();
    // ä¿®æ”¹å’Œæ·»åŠ æ˜¯åœ¨ä¸€å—çš„
    aboutData.length > 0
      ? props.BlogActions.asyncAboutUpdateAction({
          id: aboutData[0]._id,
          content: val.content,
          checked: isChecked,
      }).then(() => {
          message.success('æ›´æ–°æˆåŠŸ');
        props.BlogActions.asyncAboutListAction(isChecked).then((res: AxiosResponse<AboutData>) => {
          let { data } = res.data;
          let updateTime = data.map((item) => item.updateTime).join('');
          let createTime = data.map((item) => item.createTime).join('');
          let content = data.map((item) => item.content).join('');
            setTimeout(() => {
              form.setFieldsValue({ content: content });
            }, 500);
            setUpdateTime(updateTime === '0' ? createTime : updateTime);
            setAboutData(data);
          });
        })
      : props.BlogActions.asyncAboutAddAction({
          checked: isChecked,
          content: val.content,
      }).then(() => {
          message.success('æ–°å¢æˆåŠŸ');
        props.BlogActions.asyncAboutListAction(isChecked).then((res: AxiosResponse<AboutData>) => {
          let { data } = res.data;
          let updateTime = data.map((item) => item.updateTime).join('');
          let createTime = data.map((item) => item.createTime).join('');
          let content = data.map((item) => item.content).join('');
            setTimeout(() => {
              form.setFieldsValue({ content: content });
            }, 500);
            setUpdateTime(updateTime === '0' ? createTime : updateTime);
            setAboutData(data);
          });
        });
  };
  // ç‚¹å‡»åˆ·æ–°æ—¶é‡æ–°è°ƒç”¨æ¥å£
  const onRefresh = () => {
    message.success('åˆ·æ–°æˆåŠŸ');
    props.BlogActions.asyncAboutListAction().then((res: AboutData) => {
      // å°†å€¼è®¾ç½®è¿›è¡¨å•æ•°æ®ä¸­
      form.setFieldsValue(res.data);
    });
  };
  // åˆ‡æ¢çŠ¶æ€
  const handleClickChecked = () => {
    setIsChecked(!isChecked);
  };
  return (
    <>
      <div style={{ height: 'calc(100vh - 180px)', overflow: 'auto' }}>
        <div style={{ marginBottom: '5px', display: 'flex', alignItems: 'center' }}>
          {isChecked ? (
            <span style={{ fontSize: '20px', marginRight: '1rem' }}>å…³äºæˆ‘</span>
          ) : (
            <span style={{ fontSize: '20px', marginRight: '1rem' }}>å…³äºæœ¬ç«™</span>
          )}
          <Switch
            checked={isChecked}
            onClick={handleClickChecked}
            style={{ fontSize: '20px' }}
          ></Switch>
        </div>

        <Form form={form} className="mx-auto">
          {isChecked ? (
            <Row>
              <Col span={24}>
                <Form.Item name="content">
                  <Editor preview={false} subfield={false} placeholder="å…³äºæˆ‘" ref={editorRef} />
                </Form.Item>
              </Col>
            </Row>
          ) : (
            <Row>
              <Col span={24}>
                <Form.Item name="content">
                    <Editor preview={false} subfield={false} placeholder="å…³äºæœ¬ç«™" ref={editorRef} />
                </Form.Item>
              </Col>
            </Row>
          )}
        </Form>
      </div>
      <Save time={updateTime} onRefresh={onRefresh} onSave={submit} />
    </>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};

export default connect(null, mapDispatchToProps)(About);
